* {
  /* outline: 1px solid lightcoral; */
}

body {
  height: 100vh;
  min-width: 720px;
  overflow-x: hidden;
  background-color: #A9FDAC;
}

 /* Welcome  */
.welcome {
  margin: 0 auto;
  display: block;
  width: 50vw;
}

.welcome h4 {
  text-align: center;
  font-family: 'Orbitron', sans-serif;
  font-size: 5rem;  
  margin: 0 0;
  letter-spacing: 5px;
  text-shadow: 4px 3px 0px #fff, 9px 8px 0px rgba(0,0,0,0.15);
}

.welcome p {
  text-align: center;
  margin-top: 2.5vh;
  font-family: 'Noto Sans', sans-serif;
  
  color: grey;
  font-size: 1rem;
}

.changing-colors {
  -webkit-animation: changing-colors 4s linear infinite alternate both;
          animation: changing-colors 4s linear infinite alternate both;
}

@-webkit-keyframes changing-colors {
  0%    { color: #196127; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #97d618;}
  33%   { color: #239a3b; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #7bc96f;}
  66%   { color: #7bc96f; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #239a3b;}
  100%  { color: #c6e48b; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #196127;}
}

@keyframes changing-colors {
  0%    { color: #196127; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #97d618;}
  33%   { color: #239a3b; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #7bc96f;}
  66%   { color: #7bc96f; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #239a3b;}
  100%  { color: #c6e48b; text-shadow: 4px 3px 0px #fff, 9px 8px 0px #196127;}
}

/* board  */

.board-area {
  margin: 0 auto;
  margin-top: 5vh;
  background-color: white;
  width: 680px;
  border-radius: 10px;
  padding-top: 10px;
  padding-bottom: 35px;
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  justify-content: center;
}

.options-wrapper {
  width: 622px;
  margin: 0 auto;
}

.options-wrapper .options {
  margin: 2px 2px 2px 2px;
  color: lightgray;
  font-family: 'Noto Sans', sans-serif;
  letter-spacing: 1px;
  display: inline-block;
  transition: 0.3s;
}

.options-wrapper .options:hover {
  color: orange;
  cursor: pointer;
}

.options-wrapper .options:nth-child(1){
  float: right;
}

.board {
  display: block;
  margin: 0 auto;
  width: 622px;
}

.board-cell:hover {
  outline: 1px solid orange;
}

.squares {
  padding: 0 0;
  display: grid;
  grid-gap: 2px;
  grid-template-rows: repeat(7, 10px);
  grid-auto-flow: column;
  grid-auto-columns: 10px;
}

.squares:hover{
  cursor: pointer;
}

.color-switch {
  display: block;
  margin: 0 auto;
  width: 120px;
}

.color {
  height: 20px;
  width: 20px;
  float: right;
  margin: 2px 2px;
}

.color:hover {
  outline: 1px solid orange;
  cursor: pointer;
}

#color1 {
  background-color: #196127
}

#color2 {
  background-color: #239a3b
}

#color3 {
  background-color: #7bc96f;
}

#color4 {
  background-color: #c6e48b;
}

#color5 {
  background-color: #ebedf0
}

/* results  */

.button-download {
  display: block;
  margin: 2.5vh auto;
}

.results {
  width: 50vw;
  height: 10vh;
  display: block;
  margin: 0 auto;
  border-radius: 10px;
  padding: 5px 10px 5px 10px;
  resize: none;
  background-color: #24292e;
  color: rgb(255, 184, 51);
}

::-webkit-scrollbar {
  width: 0px;
}

/* instructions */

.instructions {
  display: block;
  margin: 0vh auto;
  width: 40vh;
  font-family: 'Noto Sans', sans-serif;
  font-size: 1rem;
}

.instructions h5 {
  margin: 2vh 0 0 0;
  color: #196127;
  text-align: center;
  font-size: 1.5rem;
  text-shadow: 1px 2px 1.5px rgba(0,0,0,0.3);
}

.instructions li {
  margin: 1vh auto;
  color: #165322;
}

.instructions span {
  background-color: #24292e;
  color: orange;
  padding: 0 5px;
  border-radius: 5px;
  white-space: nowrap;
}

.made-by-polevoy {
  position: absolute;
  text-decoration: none;
  color: grey;
  font-size: 0.8rem;
  top:1vh;
  right: 1vw;
}

.instructions a:hover {
  color: orange;
}

body button {
	background-color:#89c403;
	border-radius:5px;
	border:1px solid #74b807;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-weight:bold;
	padding:5px 20px;
	text-decoration:none;
  text-shadow:0px 1px 0px #005d13;
  transition: 0.3s;
}

body button:hover {
	background-color:#77a809;
}

body button:active {
	position:relative;
	top:1px;
}

/* faq  */
.button-faq {
  display: block;
  margin: 0 auto;
}

.faq-window {
  width: 80vw;
  height: 70vh;
  background-color: white;
  position: absolute;
  top: 25%;
  left: 10%;
  border-radius: 10px;
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -webkit-animation: left-to-right 0.3s cubic-bezier(0.250, 0.500, 0.250, 0.900) both;
          animation: left-to-right 0.3s cubic-bezier(0.250, 0.500, 0.250, 0.900) both;
}

@-webkit-keyframes left-to-right {
  0% { -webkit-transform: translateX(-1000px); transform: translateX(-1000px); opacity: 0; }
  100% { -webkit-transform: translateX(0); transform: translateX(0); opacity: 1; }
}

@keyframes left-to-right {
  0% { -webkit-transform: translateX(-1000px); transform: translateX(-1000px); opacity: 0; }
  100% { -webkit-transform: translateX(0); transform: translateX(0); opacity: 1; }
}

.faq-window ul {
  list-style-type: none;
  display: block;
  margin: 0 auto;
  width: 60vw;
}

.faq-window h4 {
  color: #196127;
  text-align: center;
  font-size: 1.5rem;
  text-shadow: 1px 2px 1.5px rgba(0,0,0,0.3);
}

.faq-window p {
  font-family: 'Noto Sans', sans-serif;
  font-size: 1rem;
  letter-spacing: 0.4px;
  color: #165322;
}

.examples-board {
  position: absolute;
  display: flex;
  height: 24vh;
  width: 80vw;
  background-color: white;
  top: 0;
  left: 10vw;
  text-align: justify;
  border-radius: 0 0 10px 10px;
  box-shadow: 0px 5px 15px -5px rgba(0,0,0,0.75);
  -webkit-animation: top-to-bottom 0.3s cubic-bezier(0.250, 0.500, 0.250, 0.900) both;
          animation: top-to-bottom 0.3s cubic-bezier(0.250, 0.500, 0.250, 0.900) both;
  justify-content: space-evenly;
}

@-webkit-keyframes top-to-bottom {
  0% { -webkit-transform: translateY(-1000px); transform: translateY(-1000px); opacity: 0; }
  100% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}

@keyframes top-to-bottom {
  0% { -webkit-transform: translateY(-1000px); transform: translateY(-1000px); opacity: 0; }
  100% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}

.example-preview {
  height: 12vh;
  width: 12vh;
  margin: 6vh 0;
  border-radius: 10px;
  border: 1px solid lightgray;
  background-color: #ebedf0;
  transition: 0.3s;
  color: gray;
  font-family: 'Noto Sans', sans-serif;
  font-size: 2.5vh;
  text-align: center;
  line-height: 12vh;
}

.example-preview:hover {
  border-bottom-color: gray;
  box-shadow: 0px 5px 5px -5px rgba(0,0,0,0.75);
}

.example-preview:active {
  position: relative;
  top: 1px;
}
